import './AdminClienteDetail.css'

import { observer } from 'mobx-react-lite'
import React, { useEffect, useRef, useState } from 'react'
import * as FIcon from 'react-feather'
import { RouteComponentProps } from 'react-router'

import { AdminItemDetail, Page } from '../../components'
import LoaderAdmin from '../../components/LoaderAdmin'
import { RUTAS, RUTAS_WEB_TRANSPORTISTA, TIPOS_REFRIGERACION_SELECT } from '../../constants'
import { openPageNow } from '../../helpers'
import {useToaster, useApiToaster, DetailViewProps} from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import { Vehiculo } from '../../services/api'

const numeral = require('numeral')
const moment = require('moment-timezone')

export const AdminVehiculoDetail: React.FC<DetailViewProps> = observer(({ history, match }) => {
  const rootStore = useStores()
  const { loginStore } = rootStore
  const [Vehiculo, setVehiculo] = useState<Vehiculo>()
  const [Loading, setLoading] = useState(true)
  const addToast = useToaster()
  const apiToaster = useApiToaster()
  const IdVehiculo = match.params.id
  const EstadoVehiculo = match.params ? (match.params as any).estado : 'moderado'

  const setup = async () => {
    obtenerVehiculo(parseInt(IdVehiculo))
  }

  useEffect(() => {
    setup()
  }, [IdVehiculo])

  const obtenerVehiculo = (idVehiculo: number) => {
    rootStore.environment.api.adminGetVehiculo(idVehiculo).then((result) => {
      setLoading(false)
      if (result.kind === 'ok') {
        setVehiculo(result.vehiculo)
      } else if (result.kind === 'not-found') {
        addToast('Vehículo no encontrado', 'warning')
        if (!loginStore.administrador) {
          openPageNow(RUTAS_WEB_TRANSPORTISTA.VEHICULOS, history)
        } else {
          openPageNow(RUTAS.ADMIN_VEHICULOS(EstadoVehiculo), history)
        }
      }
    })
  }

  const aprobarVehiculo = () => {
    if (!Vehiculo) {
      return
    }
    // eslint-disable-next-line no-restricted-globals
    if (!confirm('Confirma que desea aprobar el vehículo?')) {
      return false
    }
    rootStore.environment.api.aprobarVehiculo(IdVehiculo).then((result) => {
      if (result.kind === 'ok') {
        apiToaster(result, 'El vehículo se aprobó exitosamente!', 'aprobar el vehículo')
        obtenerVehiculo(IdVehiculo)
      } else {
        if (result.kind === 'bad-data') {
          // @ts-ignore
          addToast(result.errors[0], {
            appearance: 'warning',
            autoDismiss: true,
          })
        }
      }
    })
  }

  const breadCrumbs = (vehiculo: Vehiculo) => {
    if (loginStore.modo_uso_app === 30) {
      return (
        <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
          <li className="breadcrumb-item">
            <a onClick={() => history.push(RUTAS_WEB_TRANSPORTISTA.INICIO)}>Inicio</a>
          </li>
          <li className="breadcrumb-item">
            <a onClick={() => history.push(RUTAS_WEB_TRANSPORTISTA.VEHICULOS)}>Vehículos</a>
          </li>
          <li className="breadcrumb-item active">
            {vehiculo.marca} {vehiculo.modelo} {vehiculo.ano}
          </li>
        </ol>
      )
    } else {
      return (
        <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
          <li className="breadcrumb-item">
            <a onClick={() => history.push(RUTAS.ADMIN)}>Inicio</a>
          </li>
          <li className="breadcrumb-item">
            <a onClick={() => history.push(RUTAS.ADMIN_VEHICULOS(EstadoVehiculo))}>Vehículos</a>
          </li>
          <li className="breadcrumb-item active">
            {vehiculo.marca} {vehiculo.modelo} {vehiculo.ano}
          </li>
        </ol>
      )
    }
  }

  return (
    <Page login_required admin>
      <LoaderAdmin visible={Loading} />
      {!Loading && Vehiculo && (
        <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
          <div className="container-fluid background-white">
            <div className="row page-titles">
              <div className="col-md-5 col-12 align-self-center">
                <h3 className="text-themecolor">
                  Vehículo - {Vehiculo.marca} {Vehiculo.modelo} {Vehiculo.ano}
                </h3>
              </div>
              <div className="col-md-7 col-12 align-self-center d-none d-md-block">{breadCrumbs(Vehiculo)}</div>
            </div>
            <div className={'mb-2 text-right'}>
              {loginStore.administrador && !Vehiculo.aprobado && (
                <button className={'btn btn-primary ml-3'} title={'Aprobar'} onClick={() => aprobarVehiculo()}>
                  <FIcon.Check size={14} /> Aprobar
                </button>
              )}
              <button
                className={'btn btn-primary ml-3'}
                title={'Editar'}
                onClick={() => {
                  if (loginStore.administrador) {
                    openPageNow(RUTAS.ADMIN_VEHICULO_UPDATE(EstadoVehiculo, Vehiculo.id), history)
                  } else {
                    openPageNow(RUTAS_WEB_TRANSPORTISTA.VEHICULO_UPDATE(Vehiculo.id), history)
                  }
                }}
              >
                <FIcon.Edit size={14} /> Editar
              </button>
            </div>
            <div className="card">
              <div className="card-body">
                {loginStore.administrador && (
                  <div className={'row'}>
                    <AdminItemDetail titulo={'ID del vehículo'} texto={Vehiculo.id} columnas={3} />
                    {loginStore.modo_uso_app === 30 && <AdminItemDetail titulo={'Driver'} texto={Vehiculo.transportista + ''} columnas={3} />}
                    {loginStore.modo_uso_app !== 30 && <div className={"col-3"}>
                      <div className={'text-gray mb-2 d-flex align-items-center'}>
                        <strong>Driver</strong>{' '}
                      </div>
                      <div className="detalle-admin-item">
                        <a href={RUTAS.ADMIN_TRANSPORTISTA_DETAIL("moderado", Vehiculo.transportista_id)}>{Vehiculo.transportista + ''}</a>
                      </div>
                    </div>}
                    <AdminItemDetail titulo={'Tipo de vehículo'} texto={Vehiculo.tipo_vehiculo.nombre} columnas={3} />
                    <AdminItemDetail
                      titulo={'Refrigeración'}
                      texto={
                        TIPOS_REFRIGERACION_SELECT.filter(
                          (val) => parseInt(val.value) === Vehiculo?.tipo_vehiculo.refrigeracion,
                        )[0].label
                      }
                      columnas={3}
                    />
                  </div>
                )}
                <div className={'row'}>
                  <AdminItemDetail titulo={'Marca'} texto={Vehiculo.marca} columnas={3} />
                  <AdminItemDetail titulo={'Modelo'} texto={Vehiculo.modelo} columnas={3} />
                  <AdminItemDetail titulo={'Color'} texto={Vehiculo.color} columnas={3} />
                  <AdminItemDetail titulo={'Año'} texto={Vehiculo.ano} columnas={3} />
                  <AdminItemDetail titulo={'Número de chapa'} texto={Vehiculo.numero_chapa} columnas={3} />
                  <AdminItemDetail titulo={'Número de chasis'} texto={Vehiculo.numero_chassis} columnas={3} />
                  <AdminItemDetail
                    titulo={'Kilometraje'}
                    texto={numeral(Vehiculo.kilometraje).format('0,0') + ' km.'}
                    columnas={3}
                  />
                  <AdminItemDetail
                    titulo={'Agregado el'}
                    texto={moment(Vehiculo.fecha_agregado).format('DD/MM/YYYY')}
                    columnas={3}
                  />
                  <AdminItemDetail
                    restriccion={loginStore.administrador}
                    titulo={'Actualizado el'}
                    texto={moment(Vehiculo.fecha_actualizado).format('DD/MM/YYYY')}
                    columnas={3}
                  />
                  <AdminItemDetail
                    restriccion={loginStore.administrador}
                    titulo={'Aprobado por'}
                    texto={Vehiculo.aprobado_por ? Vehiculo.aprobado_por : 'PENDIENTE'}
                    columnas={3}
                  />
                  <AdminItemDetail
                    titulo={'Habilitación vencimiento'}
                    texto={moment(Vehiculo.habilitacion_vencimiento).format('DD/MM/YYYY')}
                    columnas={3}
                  />
                  <AdminItemDetail titulo={'Habilitación frente'} imagen={Vehiculo.habilitacion_frente} columnas={3} />
                  <AdminItemDetail titulo={'Habilitación dorso'} imagen={Vehiculo.habilitacion_dorso} columnas={3} />
                  <AdminItemDetail titulo={'Poliza de seguro'} imagen={Vehiculo.poliza_seguro} columnas={3} />
                  <AdminItemDetail titulo={'Aprobado'} texto={Vehiculo.aprobado ? 'Si' : 'No'} columnas={3} />
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </Page>
  )
})

export default AdminVehiculoDetail
